<template>
  <div class="my-popups">
    <div class="modal d-block">
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">
              {{ $t('UserDetail') }}
            </h5>
            <button v-on:click="ClosePopup()" type="button" class="close" data-original-title="Close"><em
                class="fa fa-times"></em></button>

          </div>
          <div class="border p-3">
            <div class="listing">
              <div class="table-responsive" style="max-height:400px;">
                <table class="table-sorting table table-bordered  mb-0" id="table28">
                  <thead class="thead-bg">
                    <tr>
                      <th class="td2">
                        {{$t("UserTitle")}}
                      </th>
                      <th class="td3">
                        
                        {{$t("Email")}}
                      </th>
                      <th class="td1 text-center">
                        
                        {{$t("Status")}}
                      </th>
                    </tr>
                  </thead>
                  <tbody>

                    <tr v-if="UserDetails.length==0">
                      <td class="text-center  text-danger no-record" colspan="3">
                       {{ $t('NoRecordfound') }}
                      </td> 
                    </tr>
                    <tr  v-for="(item,itemIndex) in UserDetails">
                      <td class="">
                       {{ item.username }}
                      </td>
                      <td class="">
                        {{ item.useremailid }}
                      </td>                     
                      <td class="td1 text-center">
                        <a v-if="item.userstatus=='Active'" class="active-disabled" statusid="1001" data-placement="left">
                          <span class="btn btn-success"> {{ item.userstatus }}</span>
                        </a>
                        <a v-else class=" active-disabled" statusid="1001" data-placement="left">
                          <span class="btn btn-danger"> {{ item.userstatus }}</span>
                        </a>
                       
                    </td>   
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import DataService from '../../services/DataService.js';



export default {
  data() {
    return {
      UserDetails:[]    
    }
  },
  created: function () {
    //debugger
    this.GetUserDetails();
  },
  methods: {
    GetUserDetails(){
      var vm =this;     
      var params = `roleId=${vm.$parent.roleIdForUserDetail}`;
            DataService.UserListPopUp(params)
                .then(function (response) {   
                  if(response.data) {
                    vm.UserDetails=response.data; 
                  }
                                      
                });  

    },
    ClosePopup() {
      var vm = this;
      vm.$parent.isShowUserDetailPopup = false;

    }
  }
}
</script>