function bindleftCommonSearchdropdown(instance, filtername) {
        var vm = instance;    
          
        if(filtername=='usertype') {
            var params = `filterby=MANAGEROLECA`;
            DataService.GetUserTypes(params)
                .then(function (response) {
                    
                    var json = response.data;                
                    if (json != null) {  
                            vm.leftSearchFields[2].listOptions =json.map((item) => {
                                const container = {};                    
                                container.name = item.user_type_desc;
                                container.value = (item.user_type_desc).toString();                    
                                return container;
                            });     
                                                        
                    }
                });  
        }   
}
export default { buttons, leftsearchSchema, listActions,TableHeader, bindleftCommonSearchdropdown };


