<template>
    <section>
        <loader :isVisible="isLoading"></loader>
        <tg-list :IsShowAction="true" ModuleName="CompanySetup" SubModuleCode="" :LegendArray="[]"
            :showCheckBox="true" IdentityColumn="rolE_ID" :listType="['List']" :ListData="FieldsData"
            :HeaderText="$t('Roles')" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
            :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData"
            :TotalRecords="TotalRecords" :SortExp="SortExp" :SortBy="SortBy" v-on:PagerButtonClick="pagerMethod"
            v-on:SortdataButtonClick="sortdata" :NorecordfoundText="$t('NoRecordfound')"
            v-on:ActionButtonClick="actionButtonClick" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:LeftsearchButtonClick="leftCommonSearch">

            <template v-slot:slotdata="{ data }">
      
                <template v-if="data.column.COLUMN_NAME == 'rolE_NAME'">                    
                        <span class="text-dark" v-if="data.row.rolE_NAME">
                            <a href="javascript:;" v-on:click="GoToManageRole(data.row.rolE_ID)"
                                class="relatedproduct" :id="data.row.rolE_ID"   :title="data.row.rolE_NAME" ><em
                                    class="text-primary" aria-hidden="true">{{ data.row.rolE_NAME }}</em></a>
                        </span>
                   
                </template>

            

                <template v-if="data.column.COLUMN_NAME == 'statuS_NAME'">
                    <!-- <a class="">
                        <span :class="{'btn d-inline-block text-center text-truncate btn-success' : data.row.statuS_NAME=='Active', 'btn d-inline-block text-center text-truncate btn-danger' : data.row.statuS_NAME=='Inactive'}">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>

                       


                    </a> -->


                    <div class="cstm-drop-btn">

                        <div  class="cstm-drop-btn">
                            <select v-model="data.row.selectedOption" @change="DrpdownUpdataStatus(data.row)" v-bind:class="data.row.selectedClass">
                                <option v-for="(option,itemIndex) in data.row.options"  :key="option.id" :value="option.value">{{ option.label }}</option>                               
                            </select>
                        </div>
                   </div>

                </template>

                <template v-if="data.column.COLUMN_NAME == 'rolE_COUNT'">
                    <a href="javascript:;" class="text-dark user_count" v-on:click="UserDetailPopup(data.row.rolE_ID)" :title="data.row.rolE_COUNT" data-toggle-tooltip="tooltip" :data-original-title="data.row.rolE_COUNT">
                    <i class="fa fa-user mr-1" aria-hidden="true"></i><span class="noti-circle noti-blue">  {{ data.row.rolE_COUNT }}</span>
                    </a>
                </template>

               
               
                

            </template>
        </tg-list>

       <CloneRole v-if="isShowCloneRolePopup" :RoleId="RoleId"></CloneRole>          
        <UserDetail v-if="isShowUserDetailPopup"></UserDetail>    
    </section>
</template>

<script>
import Schema from '../Roles/RolesSchema.js'
import DataService from "../../services/DataService.js";
import CloneRole from './CloneRole.vue';
import UserDetail from './UserDetail.vue';
import loader from '../../components/Common/Loader.vue'

export default {

    components: {
        CloneRole,        
        UserDetail,   
        loader
    },
    data() {
        return {
            isLoading: false,
            isShowModulePopup: false,
            isShowUserDetailPopup: false,
            PageSize: 10,
            PageNumber: 1,
            TotalPages: 0,
            SortBy: '',
            SortExp: '',
            CurrentPage: 1,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            Headers: Schema.TableHeader,
            searchCondition: '',
            TotalRecords: 0,
            FieldsData: [],
            isShowCloneRolePopup: false,
            RoleId: null,  
            RoleName:'',
            showRole:false ,
            roleIdForUserDetail:'',
               
        }
    },

    created: function () {
        var vm = this;
        vm.FetchData();
         Schema.bindleftCommonSearchdropdown(vm, "usertype");

    },
    methods:
    {
        FetchData: function () {
            var vm = this;
            vm.isLoading = true;
                     
          var params = `sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&${vm.searchCondition}`;
            DataService.GetRoles(params).then(function (response) {
                vm.isLoading = false;
                if (response.data != null) {
                    vm.FieldsData = response.data;

                    vm.FieldsData.forEach(row => {
                        row.isCheckBoxDisabled = false;
                        var options= [
                        { id: 1, label: 'Active', value: '1001' },
                        { id: 2, label: 'InActive', value: '1002' }
                       ];
                        row.options=options;
                        row.selectedOption=row.statuS_NAME=='Active'?1001:1002;
                        row.selectedClass=row.statuS_NAME=='Active'?'btn-success':'btn-danger';
                        if (row.rolE_COUNT>0) {
                            row.isCheckBoxDisabled = true;
                        }
                        else {
                            row.isCheckBoxDisabled = false;
                        }

                    })
               
                    vm.Headers.forEach((column) => {
                        column.settings = null;
                        
                        if (column.COLUMN_NAME == 'rolE_NAME') {
                            column.settings = {
                                isInSlot: true,
                                clickEvent: vm.clickEventOfColumn,

                            }
                        } 

                        if (column.COLUMN_NAME == 'statuS_NAME') {
                            column.settings = {                                
                                isInSlot: true
                            }
                        } 

                        if (column.COLUMN_NAME == 'rolE_COUNT') {
                            column.settings = {                                
                                isInSlot: true
                            }
                        } 

                  
                    })
                    if (vm.FieldsData != null) {
                        vm.TotalRecords = vm.FieldsData[0].totaL_RECORDS;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;

                    }
                    else {
                        vm.noRecord = true;
                        vm.FieldsData = [];
                        vm.TotalRecords = 0;
                        vm.isLoading = false;
                    }

                }
                else {
                    vm.noRecord = true;
                    vm.FieldsData = [];
                    vm.TotalRecords = 0;
                    vm.isLoading = false;
                }
            })
        },
        DrpdownUpdataStatus(item){
            var vm=this;   
            vm.isLoading = true;
                var params = `roleid=${item.rolE_ID}&statusId=${parseInt(item.selectedOption)}&tableName=TALYGEN_ROLE&primaryKey=ROLE_ID`;
                        DataService.UpdateRoleStatus(params)
                            .then(function (response) {   
                            if(response.data) {
                                vm.FetchData();
                            }

                            vm.isLoading = false;
                                      
                });  

        },
        GoToManageRole(roleId){
        var vm=this;
        vm.$router.push({
                path: '/Roles/UpdateRoles/'+roleId
          });

        },
        UserDetailPopup(roleId){
            var vm=this;
            vm.roleIdForUserDetail=roleId;
            vm.isShowUserDetailPopup=true;
        },
        renderActions: function (row) {
            
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Clone':
                        action.isVisible = true;
                        break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },

        leftCommonSearch: function (value) {
            
            this.PageNumber = value.PageNumber;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },

        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this;
            
            switch (callbackfunction) {
                case 'CloneField':
                    vm.AddCloneRole(item);
                    break;
            }
        },

        commonHeaderButtonClick: function (event) {
           //console.log("event.callbackfunction",event.callbackfunction)
            switch (event.callbackfunction) {
                case "AddRole":
                    this.AddRole();                    
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },


        AddCloneRole: function (event) {

            var vm = this;
            if (event.rolE_ID != null || event.rolE_ID != undefined) {
                vm.RoleId = event.rolE_ID;
                vm.RoleName=event.rolE_NAME;
                return vm.isShowCloneRolePopup = true;
            }
            else {
                return vm.isShowCloneRolePopup = false;

            }


        },

        clickEventOfColumn: function (row, column) {
            console.log(row,column.COLUMN_NAME)
            if (column.COLUMN_NAME == 'rolE_NAME') {
                this.$router.push({
                    name: 'ManageRole',
                    params: { RoleId: row.rolE_ID }
                });
            }
        },
        commonDeleteData: function (id) {
            
            var vm = this;
            var val = "";
            vm.isLoading = true;

            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Report")), function () {
                    var postJSON = { ids: val, CRM: "", Deals: "", refCode: "TALYGEN_ROLE" }

                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonCommonDelete(objectPostString).then(function (response) {
                        var box = "";
                        if (response.status == 200) {
                            $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                            $("#DeleteMultiple").addClass("disabled");
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Record')]), "success", true, vm.$t("Alert"));
                            vm.PageNumber = parseInt(vm.PageNumber) - 1;
                            if (vm.PageNumber <= 0) {
                                vm.PageNumber = 1;
                            }
                            vm.FetchData();
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Report')]), "Success", true, vm.$t("Alert"));
                        }
                    },);
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
      
        AddRole: function (ths) {
                var vm=this;
                
                vm.$router.push({
                        path: '/Roles/AddRoles'
                });
        },
        hidePopup() {
            var vm = this;
            vm.FetchData();

        },

        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },


        CloseAddPopup: function () {
            var vm = this;
            vm.showLayoutPopup = false;
            vm.FetchData();

        },

        pagerMethod: function (value) {

            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },

    }
}
</script>


<style>
span#action-list {
    display: flex;
    align-items: center;
}
.vue-treeselect__input {
  border: 1px solid #abb5bf;
  padding: 4px 12px;
}
.vue-treeselect__single-value {
  padding-left: 12px;
}

</style>