<template>
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('CopyRoleName') }}
                        </h5>
                        <button v-on:click="ClosePopup()" type="button" class="close" data-original-title="Close"><em
                                class="fa fa-times"></em></button>

                    </div>
                    <div class="border p-3">
                        <DynamicForm lang="en" v-if="DataLoaded" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></DynamicForm>
                        <div class="col-lg-6">
                            <div class="py-2">
                                <span class="mandatory"> {{ $t('MandatoryString') }} </span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import DataService from '../../services/DataService.js';
import { DynamicForm } from 'tg-controls_cli/dist/myLib.common'


export default {
    components: { DynamicForm },

    props: {
        RoleId: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            DataLoaded: false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [

                        {
                            astype: "TextField",
                            label: "To Role Name",
                            name: "RoleName",
                            value: "",
                            placeholder: "Enter Role Name",
                            validationRules: { "required": true },

                        }

                    ]
                },

            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i> Save",
                    //onClick: this.onSubmit
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i> Cancel",
                    onClick: this.ClosePopup
                }
            ],
            is_duplicate:false,
        }
    },
    created: function () {
        
        //this.RoleId
        this.DataLoaded = true;
    },
    methods: {

        ClosePopup() {
            var vm = this;
            vm.$parent.isShowCloneRolePopup = false;

        },

        onSubmit(itm, val) {
            var vm = this;
           

           var params = `existingRoleId=${vm.RoleId}&roleName=${itm.RoleName}&type=TEMPLATE_ROLE&id=&refid=`;
            DataService.CopyRole(params)
                .then(function (response) {   
                  if(response.data=="exist") {
                 
                    vm.ShowAlert(vm.$t("RoleNameExist"), "warning", true, "Alert");
                    return true;
                  }
                  else if(response.data==1){
                    vm.ShowAlert(vm.$t("RolecopySuccessfully"), "success", true, "Alert");
                    vm.$parent.FetchData();
                    vm.$parent.isShowCloneRolePopup = false;
                  }
                                      
             });  
        }
    }
}
</script>